import Paginate from 'vuejs-paginate'
export default {
  name: 'list',
  components: {Paginate},
  props: [],
  data () {
    return {
      total: 20,
      status: '',
      organization_id: '',
      sort: 'desc',
      search: ''
    }
  },
  created () {
    this.$store.dispatch('getData', { page: 1 })
    this.$store.dispatch('getOrganization')
  },
  computed: {
    users () {
      return this.$store.getters.users
    },
    last_page () {
      return this.$store.getters.last_page
    },
    total_user () {
      return this.$store.getters.total_user
    },
    organizations () {
      return this.$store.getters.organizations
    }
  },
  mounted () {
  },
  methods: {
    clickCallback (pageNum) {
      this.$store.dispatch('getData', { page: pageNum, total: this.total })
    },
    changeTotal () {
      this.$store.dispatch('getData', { page: 1, total: this.total })
    },
    filter () {
      this.$store.dispatch('getData', { page: 1, total: this.total, search: this.search, sort: this.sort, status: this.status, organization_id: this.organization_id })
    }
  }
}



