import Full from '@/containers/Full'
import Dashboard from '@/views/Dashboard'
import Login from '@/components/login'
import Document from '@/components/document'
import ListDocument from '@/components/document/list-document'
import CreateDocument from '@/components/document/create-document'
import User from '@/components/user'
import ListUser from '@/components/user/list'

import Module from '@/components/module'
import ListModule from '@/components/module/list'
import CreateModule from '@/components/module/create'

import Question from '@/components/question'
import ListQuestion from '@/components/question/list'
import EditQuestion from '@/components/question/edit'
import ShowQuestion from '@/components/question/show'
import CreateQuestion from '@/components/question/create'
import ModuleQuestion from '@/components/question/module'
import ListModuleQuestion from '@/components/question/module/list-module'

import Test from '@/components/test'
import ShowTest from '@/components/test/show'
import ListTest from '@/components/test/list'
import CreateTest from '@/components/test/create'

// import PageNotFound from '@/components/pages/page404'

export default [
  {
    path: '/',
    redirect: '/dashboard',
    name: 'Home',
    component: Full,
    meta: {
      requiresAuth: true
    },
    children: [
      {
        path: 'dashboard',
        name: 'Dashboard',
        component: Dashboard
      },
      {
        path: 'document',
        redirect: '/document/list',
        name: 'Document',
        component: Document,
        children: [
          {
            path: 'list',
            name: 'Document list',
            component: ListDocument
          },
          {
            path: 'create',
            name: 'Document create',
            component: CreateDocument
          }
        ]
      },
      {
        path: 'module',
        redirect: '/module/list',
        name: 'Module',
        component: Module,
        children: [
          {
            path: 'list',
            name: 'Module list',
            component: ListModule
          },
          {
            path: 'create',
            name: 'Module create',
            component: CreateModule
          }
        ]
      },
      {
        path: 'question',
        redirect: '/question/module',
        name: 'Question',
        component: Question,
        children: [
          {
            path: 'module',
            name: 'Module question',
            redirect: 'module/list',
            component: ModuleQuestion,
            children: [
              {
                path: 'list',
                name: 'Module List',
                component: ListModuleQuestion
              },
              {
                path: ':module_id/list',
                name: 'List Question',
                component: Question,
                redirect: ':module_id/list',
                children: [
                  {
                    path: 'show/:id_question',
                    name: 'Show Question',
                    component: ShowQuestion
                  },
                  {
                    path: 'edit/:id_question',
                    name: 'Edit Question',
                    component: EditQuestion
                  },
                  {
                    path: '',
                    name: 'list Question',
                    component: ListQuestion
                  }
                ]
              }
            ]
          },
          {
            path: 'create',
            name: 'Question create',
            component: CreateQuestion
          }
        ]
      },
      {
        path: 'test',
        redirect: '/test/list',
        name: 'Test',
        component: Test,
        children: [
          {
            path: 'list',
            name: 'Test list',
            component: ListTest
          },
          {
            path: 'create',
            name: 'Test create',
            component: CreateTest
          },
          {
            path: 'show/:id',
            name: 'Show test',
            component: ShowTest
          }
        ]
      },
      {
        path: 'user',
        redirect: '/user/list',
        name: 'User',
        component: User,
        children: [
          {
            path: 'list',
            name: 'User list',
            component: ListUser
          }
        ]
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  }
  // {
  //   path: '/404',
  //   component: PageNotFound
  // },
  // {
  //   path: '*',
  //   redirect: '/404'
  // }
]



