import UserSevice from '@/service/user'
import OrganizationSevice from '@/service/organization'
export default {
  getData ({commit}, payload) {
    UserSevice.getUser(payload.page, payload.total, payload.search, payload.sort, payload.status, payload.organization_id).then(res => {
      if (res.status === 200) {
        commit('setData', res.data)
        // window.scroll(0, 0)
      }
    })
  },
  getOrganization ({commit}, payload) {
    OrganizationSevice.getOrganization().then(res => {
      if (res.status === 200) {
        commit('setOrganization', res)
        // window.scroll(0, 0)
      }
    })
  }
}



