import { HTTP } from '@/CallAPI'
import Helper from '@/Helper'
export default {
  name: 'app-login',
  props: [],
  data () {
    return {
      errors: [],
      message: '',
      user: {}
    }
  },
  // beforeRouteEnter (to, from, next) {
  //   HTTP.get('/getuser').then(res => {
  //     next({path: '/'})
  //   }).catch(err => {
  //     console.log(err)
  //     next()
  //   })
  // },
  created () {
  },
  computed: {

  },
  mounted () {
  },
  methods: {
    login () {
      HTTP.post('/login', this.user).then(res => {
        if (res.status === 200) {
          Helper.setUserLocalStorage(res.data)
          this.$router.go(-1)
        }
      }).catch(err => {
        if (err.status === 422) {
          this.errors = err.data.errors
        }
        if (err.status === 500) {
          this.message = 'Đăng nhập thất bại'
        }
      })
    }
  }
}



