import { HTTP } from '@/CallAPI'
function getUser (page = 1, total = 20, search = '', sort = 'desc', status = '', organization_id = '') {
  return HTTP.get('/users', {
    params: { page, total, search, sort, status, organization_id }
  })
}
export default {
  getUser
}



