import Ckeditor from 'vue-ckeditor2'

export default {
  name: 'edit',
  components: {Ckeditor},
  props: [],
  data () {
    return {
      content: 'Bootstrap 4 Components for Vue.js 2 ?',
      config: {
        toolbar: [
          [ 'Bold', 'Italic', 'Underline', 'Strike', 'Subscript', 'Superscript' ]
        ],
        height: 300
      }
    }
  },
  computed: {

  },
  mounted () {

  },
  methods: {
    back () {
      this.$router.go(-1)
    }
  }
}



