import Ckeditor from 'vue-ckeditor2'

export default {
  name: 'create',
  components: {Ckeditor},
  props: [],
  data () {
    return {
      content: '',
      config: {
        toolbar: [
          [ 'Bold', 'Italic', 'Underline', 'Strike', 'Subscript', 'Superscript' ]
        ],
        height: 300
      }
    }
  },
  computed: {

  },
  mounted () {

  },
  methods: {

  }
}



